import moment from 'moment'

const graphFormat = 'YYYY-MM-DD'

const graphTimes = [
    { key: 'День', text: 'День', value: JSON.stringify([moment().subtract(1, 'days').format(graphFormat), moment().format(graphFormat)]) },
    { key: 'Неделю', text: 'Неделю', value: JSON.stringify([moment().subtract(7, 'days').format(graphFormat), moment().format(graphFormat)]) },
    { key: 'Месяц', text: 'Месяц', value: JSON.stringify([moment().subtract(1, 'months').format(graphFormat), moment().format(graphFormat)]) },
    { key: 'Год', text: 'Год', value: JSON.stringify([moment().subtract(1, 'years').format(graphFormat), moment().format(graphFormat)]) },
]

export default {
    graphTimes,
}
